@import "normalize.css";
@import "font-awesome/css/font-awesome.min.css";
@import "@fontsource/source-sans-pro/index.css";
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
}
*::before, *::after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}
@media (max-width: 768px) {
  html {
    font-size: 50%;
  }
}

html,
body {
  font-family: "Source Sans Pro", sans-serif;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
}

body {
  font-size: 1.6rem;
}

[disabled] {
  opacity: 0.7;
  cursor: not-allowed;
}

button {
  cursor: pointer;
  border: none;
}

section {
  padding: 3.6rem 0;
}

.container {
  max-width: 120rem;
  display: flex;
  margin: auto;
  padding: 0 1.6rem;
}

.button {
  display: inline-block;
  color: #ffffff;
  background-color: #55a0d9;
  width: fit-content;
  font-weight: 700;
  padding: 1.2rem 3rem;
  border-radius: 5px;
  transition: all 150ms linear 0s;
}
.button:hover {
  opacity: 0.9;
}

.search-overlay {
  visibility: hidden;
  opacity: 0;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 110;
  position: fixed;
  top: 50%;
  left: 50%;
  width: 100vw;
  height: 100vh;
  transform: translate(-50%, -50%);
  transition: opacity 0.3s, transform 0.3s, visibility 0.3s;
  box-sizing: border-box;
  background: rgba(255, 255, 255, 0.63);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
}
@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.search-overlay .spinner-loader {
  opacity: 0;
  position: absolute;
  top: 15%;
  left: 50%;
  border-radius: 50%;
  width: 4.8rem;
  height: 4.8rem;
  border: 0.4rem solid rgba(0, 0, 0, 0.3);
  border-top-color: #55a0d9;
  animation: spin 0.6s infinite linear;
  -webkit-animation: spin 0.6s infinite linear;
  transition: opacity 0.5s ease-in-out;
}
.search-overlay .spinner-loader.show {
  opacity: 1;
}
.search-overlay .search-section {
  background-color: rgba(0, 0, 0, 0.1);
  padding: 1rem 0;
}
.search-overlay .search-section .container {
  gap: 1rem;
  align-items: center;
}
.search-overlay i {
  color: #55a0d9;
  font-size: 2.8rem;
  cursor: pointer;
}
.search-overlay.on {
  visibility: visible;
  opacity: 1;
}
.search-overlay input {
  flex: 1;
  box-sizing: border-box;
  border: none;
  padding: 15px 0;
  margin: 0;
  background-color: transparent;
  font-size: 1.8rem;
  font-weight: 300;
  outline: none;
  color: #55a0d9;
}

.header {
  padding: 0.5rem 0;
  box-shadow: 0px 2px 6px -4px rgba(0, 0, 0, 0.2);
}
.header .container {
  justify-content: space-between;
  align-items: center;
}
.header img {
  width: 70px;
}
.header nav ul {
  display: flex;
  height: 100%;
  align-items: center;
  font-size: 1.4rem;
  gap: 2rem;
}
.header nav ul li {
  height: 100%;
  display: flex;
  align-items: center;
  padding: 2rem 0;
  text-transform: uppercase;
  transition: all 250ms linear 0s;
}
.header nav ul li:hover {
  background: rgba(255, 255, 255, 0.15);
}
.header nav ul li a {
  color: #2c3e50;
}
@media (max-width: 768px) {
  .header {
    padding: 1.6rem 0;
  }
  .header img {
    width: 120px;
  }
  .header nav {
    display: none;
  }
  .header .menu-toggle {
    width: 28px;
  }
  .header .menu-toggle .one,
  .header .menu-toggle .two,
  .header .menu-toggle .three {
    background-color: #ffffff;
    height: 3px;
    width: 100%;
    border-radius: 6px;
    margin: 5px auto;
    transition-duration: 0.3s;
  }
  .header .menu-section.on {
    position: absolute;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #2c3e50;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .header .menu-section.on .menu-toggle {
    position: absolute;
    right: 20px;
    top: 18px;
  }
  .header .menu-section.on .menu-toggle .one {
    transform: rotate(45deg) translate(0px, 4px);
  }
  .header .menu-section.on .menu-toggle .two {
    opacity: 0;
  }
  .header .menu-section.on .menu-toggle .three {
    transform: rotate(-45deg) translate(7px, -12px);
  }
  .header .menu-section.on nav {
    display: block;
  }
  .header .menu-section.on nav ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .header .menu-section.on nav ul li {
    width: 100%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .header .menu-section.on nav ul li a {
    transition-duration: 0.5s;
    font-size: 3rem;
  }
}

.hero .container {
  justify-content: space-between;
}
@media (max-width: 768px) {
  .hero .container {
    flex-direction: column;
    text-align: center;
    align-items: center;
  }
  .hero .container img {
    order: -1;
  }
}
.hero img {
  width: 450px;
  border-radius: 8px;
}
.hero .content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
}
.hero .content h2 {
  color: rgb(75, 75, 75);
  font-size: 3.2rem;
  line-height: 5rem;
}
.hero .content p {
  color: #2c3e50;
  line-height: 1.2rem;
}
@media (max-width: 768px) {
  .hero .content {
    margin-top: 4rem;
    align-items: center;
  }
  .hero .content p {
    font-size: 1.8rem;
    line-height: 2.6rem;
  }
}